import React from 'react'

function Card (props) {
  function handleClick() {
    props.onCardClick(props.card);
  }

  return(
    <article className="card">
      <button className="card__delete-button page-button"></button>
      <img src={props.card.link}
      alt={props.card.name}
      className="card__image"
      onClick={handleClick}
      />
      <div className="card__container">
        <h2 className="card__title hide-overflow">{props.card.name}</h2>
        <div className="card__like-container">
          <button type="button" className="card__like-button page-button"></button>
          <span className='card__like-counter'>{props.card.likes.length}</span>
        </div>
      </div>
    </article>
  )
}

export default Card
